___CSS_LOADER_EXPORT___.push([module.id, `:root {
    /* colors */
    --clr-bkg: #2d303f;
    --clr-label: #c8c8c8;
    --clr-invalid: #ff0000;
    --clr-valid: #93a5e8;
    --clr-input: #3a3e4e;
    --clr-cta: #6d7ebe;

    /* padding */
    --pd-cta: 0.5rem;
    --pd-input: 0.6rem;

    /* font sizes */
    --fs-placeholder: 0.75rem;
}

body {
    background-color: var(--clr-bkg);
    font-family: 'Lato', sans-serif;
}

h1 {
    /* color: white; */
    /* width: max-content; */
    /* text-align: center; */
    /* margin: 0 auto; */
    margin-bottom: 1rem;

    span.highlight {
        color: var(--clr-valid);
    }
}

form {
    /* center form on the page */
    width: 20%;
    margin: 5rem auto;

    /* flex */
    display: flex;
    flex-direction: column;
    gap: 1.5rem;

    color: white;

    /* childs */
    div {
        display: flex;
        flex-direction: column;
        gap: 0.5rem;
    }

    label {
        color: var(--clr-label);
    }

    input {
        background-color: var(--clr-input);
        color: white;
        padding: var(--pd-input);
        padding-left: 0.5rem;
    }

    input::placeholder {
        font-size: var(--fs-placeholder);
    }

    .country-zip {
        flex-direction: row;
        justify-content: space-between;

        select {
            width: 10%;
        }

        div.zipcode {
            width: 40%;
        }
    }

    button {
        background-color: var(--clr-cta);
        color: white;
        padding: var(--pd-cta);

        &:hover {
            background-color: var(--clr-valid);
        }
    }

    input,
    button {
        border-radius: 8px;
    }
}

input.invalid {
    border: 2px solid var(--clr-invalid);
}

input.valid {
    border: 2px solid var(--clr-valid);
}
