___CSS_LOADER_EXPORT___.push([module.id, `.slider-container {
    margin: 0 auto;
    width: 40vw;
}

.slider {
    display: flex;
    justify-content: center;
    align-items: center;

    position: relative;

    img {
        width: 100%;
        border-radius: 1rem;
    }

    img.hidden {
        display: none;
    }

    .arrowLeft,
    .arrowRight {
        box-sizing: border-box;
        /* width: 150%; */
        border: 1px solid black;
        border-radius: 100%;
        height: 4rem;
        padding: 1rem;
        font-size: 3rem;
        background-color: var(--clr-light);
        color: white;

        position: relative;

        /* flex */
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .arrowLeft:hover,
    .arrowRight:hover {
        background-color: white;
        color: black;
    }

    .arrowLeft {
        left: 5rem;
    }

    .arrowRight {
        right: 5rem;
    }

    .arrowLeft.hidden,
    .arrowRight.hidden {
        display: none;
    }
}
