import { html } from '@github/jtml';
import './dropdown.css';
import clickHandler from './event';

const li = (word) => html`<li onclick="${clickHandler}">${word}</li>`;
const element = (data) => data.map(li);

/**
 * Creates a dropdown menu.
 * @param {object} obj - an object with options property containing an array of words for dropdown options.
 */
export default function Dropdown(obj) {
    return html`
        <div class="dropdown-component">
            <label for="dropdown">${obj.label}:</label>
            <div id="dropdown" onclick="${clickHandler}">
                <span class="placeholder">Choose</span>
            </div>

            <ul class="options hidden">
                ${element(obj.options)}
            </ul>
        </div>
    `;
}
