___CSS_LOADER_EXPORT___.push([module.id, `:root {
    --dropdown-blue: rgb(35, 92, 226);
    --dropdown-light: rgba(173, 172, 172, 0.394);
}

.dropdown-component {
    display: inline-block;
    width: 8rem;
    text-align: center;
    /* display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.2rem; */
}

#dropdown {
    color: white;
    background-color: var(--dropdown-blue);

    padding: 0.5rem 1rem;
    border-radius: 4px;

    span {
        word-wrap: break-word;
    }
    /* span.selected {
        position: fixed;
        top: 0;
    } */
}

#dropdown::after {
    content: '▼';
    font-size: 0.6rem;
}

ul.options {
    width: 100%;
    height: 8rem;
    list-style: none;
    color: black;

    /* padding - margin */
    padding: 0;
    margin: 0;

    /* long content */
    overflow-y: auto;
    word-wrap: break-word;

    /* animation */
    animation: drop 500ms ease-out;

    li {
        text-align: center;
        background-color: var(--dropdown-light);
        padding: 0.2rem;
    }

    li:hover {
        background-color: var(--dropdown-blue);
        color: White;
    }
}

ul.hidden {
    display: none;
}

@keyframes drop {
    1% {
        transform: translateY(-5%);
    }
}
